.header:hover{background-color:#0d0d0dc2}.card-2 .wrapper{background:url(https://tvseriescritic.files.wordpress.com/2016/10/stranger-things-bicycle-lights-children.jpg) center/cover no-repeat}.card-2 .wrapper:hover .menu-content span{transform:translate(-50%, -10px);opacity:1}.card-2 .header{color:#fff;padding:1em}.card-2 .header::before,.card-2 .header::after{content:"";display:table}.card-2 .header::after{clear:both}.card-2 .header .date{float:left;font-size:12px}.card-2 .menu-content{float:right}.card-2 .menu-content li{margin:0 5px;position:relative}.card-2 .menu-content span{transition:all .3s;opacity:0}.card-2 .data{color:#fff;transform:translateY(calc(70px + 4em));background:linear-gradient(#0000008f, #000)}.card-2 .date *{margin-left:5px}.card-2 .title a{color:#fff}.card-2 .button{display:block;width:100px;margin:2em auto 1em;text-align:center;font-size:12px;color:#fff;line-height:1;position:relative;font-weight:700}.card-2 .button::after{content:"→";opacity:0;position:absolute;right:0;top:50%;transform:translate(0, -50%);transition:all .3s}.card-2 .button:hover::after{transform:translate(5px, -50%);opacity:1}